.opentradesbody{
    font-size: 4vw;
}

.directionalArrowUp{
    color: green
}

.directionalArrowDown{
    color:red
}

.profit{
    color: green
}

.loss{
    color:red
}

.CustomModalHeader{
    .modal-title{
        display: flex;
        width: 100%;
        justify-content: space-between;
    }
}
.modal-footer{
    .managementbuttons{
        display: flex;
        flex-direction: row;
        justify-content: space-around;
        width: 100%;
        .managetradebutton{
            width: 30%;
            font-size: .75em;
            white-space: normal;
        }
    }
}


.tradedetail{
    white-space: pre-line;
    font-weight: bold;  
    .tradedata{
        background: rgb(228, 216, 161);
        margin-bottom: 5px;
        border-radius: 3px;
        padding: 5px;
    }
    .entrycomments{
        background: rgb(221, 217, 217);
        margin-bottom: 5px;
        border-radius: 3px;
        padding: 5px;
        .positionadded{
            color: blue;
            font-style: italic;
        }
    }
    .gainrisk{
        margin-bottom: 5px;
        border-radius: 3px;
        padding: 5px;
        &.profitable{
            background: rgb(66, 214, 85);
        }
        &.loser{
            background: rgb(236, 95, 85);
        }
    }
}

#comments{
    height: 5em;
    width:100%;
}

.riskDisplay{
    white-space: pre-line;
}

@media screen and (min-width: 700px) {
    .opentradesbody {
        font-size: 24px;
    }
    .opentradescaption{
        font-size: 24px;
    }
}