.cardx {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-around;
    margin:10px;
    width:150px;
    background:#ffffff;
    border-radius: 3px;
    font-size: 1em;
    cursor: default;
    &.lit{
      background: white;
      transform: translateY(-10px);
      transform: translatex(-2px);
      transform: scale(1.1);
      box-shadow: 10px 5px 5px black;
    }
    &.usd{
      background: #f0d8b9;
    }
    .symbol{
      color: #913232;
      font-weight: bold;
      font-family: "Spectral SC";

      .strength{
        color: #315391;
        font-weight: bold;
        font-family: "Spectral SC";
      }
    }
    .events{
      color: #ffffff;
      font-size: .75em;
      font-weight: bold;
      font-family: 'Fredoka One', cursive;
      cursor: pointer;

      &.soon{
        animation-name: flash;
        animation-duration: 1s;
        animation-iteration-count: infinite;
      }
    }
  }
  .title{
    text-align: center;
    font-weight: bold;
    font-family: "Spectral SC";
  }

  @keyframes flash {
    from {color: #d9dde3;}
    to {color: #6c757d;}
}